body, html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:  rgb(41, 41, 41);
}

* {
  color: rgb(180, 180, 180);
  transition: 0.8s;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.zone {
    font-size: 2em;
    transition: all 0.3s linear;
  }
  
  .width {
    padding: 0 10%;
  }

  a {
    color: rgb(180, 180, 180);
    text-decoration: none;
  }

/* NAV section*/
  
  
.main-nav {
    display: flex;
    justify-content: center;
    list-style: none;
    font-size: 0.6em;
    margin: 0;
    padding: 0;
    font-weight: bold;
  }
  
  .main-nav:hover {
    background: rgba(211, 210, 210, 0.664);
    border-radius: 0% 0% 50% 50%;
  }

  li {
    padding: 20px;
  }
  
  li a:hover {
    color: rgb(27, 27, 27);
  }
  
  .sticky {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
  }
  
  
  @media screen 
  and (min-width: 100px) 
  and (max-width: 900px) 
  and (-webkit-min-device-pixel-ratio: 1) { 
    .main-nav {
      font-size: 0.3em;
    }
}
/* cover */

  .container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 7% 0;
  }
  

  .intro {
    margin: 30px 0;
    width: 90%;
    font-size: 20px;
  }

  .intro1{
    font-size: 30px;
  }

  .intro2 {
    font-size: 24px;
    text-align: center;
    width: 50%;
    position: relative;
    top: 100px;
    left: 70px;   
    box-shadow: 1px -3px 50px rgb(107, 107, 107);
    border-radius: 30% 1% 1% 1%;
  }

  .intro2 a {
    color: rgb(180, 180, 180);
  }
  
  .intro2 a:hover {
    font-size: 26px;
  }
  
  .profile-pic {
    width: 45%;
    box-shadow: 5px 0px 30px rgb(26, 26, 26);
    border-radius: 2%;
  }

  .profile-pic:hover{
    box-shadow: 1px 0px 20px rgb(107, 107, 107);
  }
  
  
  @media screen 
  and (min-width: 100px) 
  and (max-width: 900px) 
  and (-webkit-min-device-pixel-ratio: 1) { 
    .container {
      flex-direction: column-reverse; 
    }

    .intro {
      font-size: 16px
    }

    .intro1 {
      font-size: 24px;
    }

    .intro2 {
      width: 50%;
      font-size: 18px;
      color: rgb(97, 96, 96);
      top: 50px;
    }

    .profile-pic {
      width: 45%;
      min-height: 40%;
    }
}
/* project */
  
.project {
    width: 80%;
    margin: 5% 0 10% 0;
  }

  
  .grid-wrapper {
    display: flex;
    margin-top: 20px;
  }

  .box {
    padding: 10px 30px;
  }

  .box img {
    width: 100%;
    height: 240px;
    opacity: 0.7;
  }

  .box img:hover {
    box-shadow: 0px 0px 30px rgb(107, 107, 107);
    opacity: 1;
  }
  
  .box-container{
    position: relative;
    top: 50px;
    margin: 30px 0;
    height: 100%;
  }
  
  .box-container:hover {
    box-shadow: 0px 0px 50px rgb(107, 107, 107);
    border-radius: 30% 1% 1% 1%;
  }
  
  .box-container h2{
    position: relative;
    left: 1%;
  }

  .box-container span{
    position: relative;
    left: 3%;
    font-size: 24px;
  }
  
  .box-container h1 {
    display: grid;
    justify-items: center;
    margin: 10% 0;
  }

  @media screen 
  and (min-width: 100px) 
  and (max-width: 800px) 
  and (-webkit-min-device-pixel-ratio: 1) { 
    .project {
      margin: 5% 0 10% 0;
    }

    .grid-wrapper {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-content: center;
      width: 100%;
      margin-top: 20px;
    }

    .box > img {
      width: 100%;
      height: 100%;
    }
  
    .box img{
      border-radius: 10px;
    }
  
    .box-container h2{
      font-size: 1em;
    }

    .box-container h1 {
      font-size: 1.5em;
      text-align: center;
    }

    .box-container span{
      font-size: 18px;
    }
}
/* contact */
  
.contact {
    display: grid;
    grid-template-columns: 60% auto;
    grid-template-rows: 50% auto;
    color: #444;
    padding: 10% 0;
  }

  .contact:hover {
    box-shadow: 1px -3px 50px rgb(107, 107, 107);
    border-radius: 30% 1% 1% 1%;
  }

  .contact1 {
    display: grid;
    justify-content: center;
    align-content: center;
    position: relative;
    top: 50%;
    left: 5%;
  }

  
  .email-me {
    width: 100%;
    text-align: center;
    color: rgb(68, 68, 68);
  }
 
  .email-me:hover {
    color: rgb(180, 180, 180);
    font-size: 35px;
  }

  .contact2 {
      display: grid;
      justify-content: center;
      margin: 10px 0;
      position: relative;
      top: 50%;
  }

  .social-media {
      padding: 10px;
      width: 100%;
      cursor: pointer;
  }

  .social-media:hover {
    background: rgba(102, 102, 102, 0.3);
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(107, 107, 107);
  }

  .note-contact {
    font-size: 20px;
  }

  

  @media screen 
  and (min-width: 701px) 
  and (max-width: 900px) 
  and (-webkit-min-device-pixel-ratio: 1) { 

    .contact {
      display: grid;
      grid-template-columns: 50% auto;
      color: #444;
      padding: 10% 0;
    }

    .contact1 {
      left: 10%;
    }
    
    .contact1 h2 {
      font-size: 1em;
    }

    .note-contact {
      font-size: 16px;
    }

    .contact2 {
      top: 20%;
    }

    .email-me {
      width: 100%;
      text-align: center;
      cursor: pointer;
      font-size: 0.7em;
      color: rgb(68, 68, 68);
    }
   
    .email-me:hover {
      color: rgb(180, 180, 180);
      font-size: 0.8em;
    }
  }


  @media screen 
  and (min-width: 100px) 
  and (max-width: 700px) 
  and (-webkit-min-device-pixel-ratio: 1) {

    .contact {
      display: grid;
      align-content: center;
      justify-content: center;
      grid-template-columns: 100%;
      color: #444;
      padding: 10% 0;
    }


    .contact1 h2 {
      font-size: 0.8em;
    }

    .contact1 {
      left: 0%;
      text-align: center;
    }

    .note-contact {
      font-size: 14px;
    }

    .email-me {
      width: 100%;
      text-align: center;
      cursor: pointer;
      font-size: 0.6em;
      color: rgb(68, 68, 68);
    }
   
    .email-me:hover {
      color: rgb(180, 180, 180);
      font-size: 0.7em;
    }
    
    .contact2 {
      display: grid;
      grid-template-columns: repeat(3, 10%);
      grid-gap: 5%;
      justify-content: center;
      top: 100%;
  }

    .social-media {
        padding: 10px;
        cursor: pointer;
    }
}
